	module.exports = {"data":{"post":{"html":"<p>MamaMaus was sent a trade magazine article on <a href=\"http://www.papascott.de/wordpress/wp-content/uploads/2007/05/mccafe.pdf\">McCafé in Germany</a> (PDF, 770K, in German) that she wanted to share. It explains the concept (coffee shop atmosphere with the convenience and value of McDonald's) and some of the details of operation.</p>\n<p>Our soon-to-be restaurant does not have a McCafé yet, but we want one as quickly as we can and our it's included in our financial planning for this year. Maybe it will look something like this (design concept Generation Wenge)...</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2007/05/mccafe-design-wenge.jpg\" alt=\"McCafé Design Wenge\" /></p>","fields":{"title":"","date":"07 May 2007","url":"https://papascott.de/2007-05-07-background-on-mccafe/"}}},"pathContext":{"slug":"/2007-05-07-background-on-mccafe/","prev":{"fields":{"title":"Corrupted UTF-8 characters with PHP and MySQL","date":"05 May 2007","slug":"/2007-05-05-corrupted-utf-8-characters-with-php-and-mysql/"}},"next":{"fields":{"title":"Fetch, Doggie, Fetch!","date":"08 May 2007","slug":"/2007-05-08-fetch-doggie-fetch/"}}}}

